.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #282c34;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  padding-bottom: 100px;
}

.background {
  border-radius: 0px;
  width: 100%;
  height: 500px;
  background-image: url(./ddbg.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 100%;
}

a {
  color: #282c34;
  text-decoration: none;
}

a:hover {
  color: #282c34;
  text-decoration: none;
}

.my-alert2{
  background: wheat;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
  
}

.my-alert2 p {
  margin-bottom: 0px;
}

.subMenuTitle {
  text-align: center;
  font-size: 25px;
}

.cartTable {
  width: 80%;
}

.btnBox {
  text-align: right;
}